import axios from "axios";
import React, { useEffect, useState } from "react";
import HomePageSidebar from "../homepage2/HomePageSidebar";

export default function ProductMainContent(props) {
  const server_url = "https://eos-adinkrah-enterprise-api.herokuapp.com/";
  const [productInfo, setProductInfo] = useState({});
  const [alreadyFetched, setAlreadyFetched] = useState(false);

  useEffect(() => {
    //masonryFunc();
    if (!alreadyFetched) {
      fetchProductInfo();
      setAlreadyFetched(true);
    }
  });

  const fetchProductInfo = () => {
    axios
      .get(`${server_url}product/${props.productId}`)
      .then((res) => {
        setProductInfo(res.data.message);
        console.log("Fetched data");
      })
      .catch((e) => console.log(e.data));
  };
  return (
    <div className="main-content-wrapper d-flex clearfix">
      <div className="mobile-nav">
        <div className="amado-navbar-brand">
          <a href="index.html">
            <img src="assets/img/core-img/logo.png" alt="" />
          </a>
        </div>
        <div className="amado-navbar-toggler">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
      <HomePageSidebar />
      <div className="single-product-area section-padding-100 clearfix">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb mt-50">
                  <li className="breadcrumb-item">
                    <a href="#">Home</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a href="#">Furniture</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a href="#">Chairs</a>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    white modern chair
                  </li>
                </ol>
              </nav>
            </div>
          </div>

          <div className="row">
            <div className="col-12 col-lg-7">
              <div className="single_product_thumb">
                <div
                  id="product_details_slider"
                  className="carousel slide"
                  data-ride="carousel"
                >
                  <ol className="carousel-indicators">
                    {/* <li
                      className="active"
                      data-target="#product_details_slider"
                      data-slide-to="0"
                      style={{
                        backgroundImage: `url("${productInfo.productImage}")`,
                      }}
                    ></li> */}
                    {/* <li
                      data-target="#product_details_slider"
                      data-slide-to="1"
                      style={{
                        backgroundImage: `url("assets/img/product-img/pro-big-2.jpg")`,
                      }}
                    ></li>
                    <li
                      data-target="#product_details_slider"
                      data-slide-to="2"
                      style={{
                        backgroundImage: `url("assets/img/product-img/pro-big-3.jpg")`,
                      }}
                    ></li>
                    <li
                      data-target="#product_details_slider"
                      data-slide-to="3"
                      style={{
                        backgroundImage: `url("assets/img/product-img/pro-big-4.jpg")`,
                      }}
                    ></li> */}
                  </ol>
                  <div className="carousel-inner">
                    <div className="carousel-item active">
                      <a
                        className="gallery_img"
                        href="assets/img/product-img/pro-big-1.jpg"
                      >
                        <img
                          className="d-block w-100"
                          src={productInfo.productImage}
                          alt="First slide"
                        />
                      </a>
                    </div>
                    {/* <div className="carousel-item">
                      <a
                        className="gallery_img"
                        href="assets/img/product-img/pro-big-2.jpg"
                      >
                        <img
                          className="d-block w-100"
                          src="assets/img/product-img/pro-big-2.jpg"
                          alt="Second slide"
                        />
                      </a>
                    </div>
                    <div className="carousel-item">
                      <a
                        className="gallery_img"
                        href="assets/img/product-img/pro-big-3.jpg"
                      >
                        <img
                          className="d-block w-100"
                          src="assets/img/product-img/pro-big-3.jpg"
                          alt="Third slide"
                        />
                      </a>
                    </div>
                    <div className="carousel-item">
                      <a
                        className="gallery_img"
                        href="assets/img/product-img/pro-big-4.jpg"
                      >
                        <img
                          className="d-block w-100"
                          src="assets/img/product-img/pro-big-4.jpg"
                          alt="Fourth slide"
                        />
                      </a>
                    </div> */}
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-lg-5">
              <div className="single_product_desc">
                <div className="product-meta-data">
                  <div className="line"></div>
                  <p className="product-price">{`$${productInfo.price}`}</p>
                  <a href="product-details.html">
                    <h6>{productInfo.productName}/</h6>
                  </a>
                  <div className="ratings-review mb-15 d-flex align-items-center justify-content-between">
                    <div className="ratings">
                      <i className="fa fa-star" aria-hidden="true"></i>
                      <i className="fa fa-star" aria-hidden="true"></i>
                      <i className="fa fa-star" aria-hidden="true"></i>
                      <i className="fa fa-star" aria-hidden="true"></i>
                      <i className="fa fa-star" aria-hidden="true"></i>
                    </div>
                    <div className="review">
                      <a href="#">Write A Review</a>
                    </div>
                  </div>
                  <p className="avaibility">
                    <i className="fa fa-circle"></i> In Stock
                  </p>
                </div>

                <div className="short_overview my-5">
                  <p>{productInfo.description}</p>
                </div>

                <form className="cart clearfix" method="post">
                  <div className="cart-btn d-flex mb-50">
                    <p>Qty</p>
                    <div className="quantity">
                      <span
                        className="qty-minus"
                        onclick="var effect = document.getElementById('qty'); var qty = effect.value; if( !isNaN( qty ) &amp;&amp; qty &gt; 1 ) effect.value--;return false;"
                      >
                        <i className="fa fa-caret-down" aria-hidden="true"></i>
                      </span>
                      <input
                        type="number"
                        className="qty-text"
                        id="qty"
                        step="1"
                        min="1"
                        max="300"
                        name="quantity"
                        value="1"
                      />
                      <span
                        className="qty-plus"
                        onclick="var effect = document.getElementById('qty'); var qty = effect.value; if( !isNaN( qty )) effect.value++;return false;"
                      >
                        <i className="fa fa-caret-up" aria-hidden="true"></i>
                      </span>
                    </div>
                  </div>
                  <button
                    type="submit"
                    name="addtocart"
                    value="5"
                    className="btn amado-btn"
                  >
                    Add to cart
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
